import React, {Component} from 'react';
// import moment from "moment";
import ItemList from "./components/item-list/item-list";
import './App.css';
import Header from "./components/Header/Header";
import Input from "./components/Input/Input";

class App extends Component {

    state = {
        trackers : [
            { id : 0, title: "Example (You can delete me)" , pause : false }
        ]
    };

    onInputSubmit = (value) => {
        let st = this.state.trackers;
        let idx = this.state.trackers.length ;
        st.push({id:idx, title: value , pause: false})
        this.setState({
            trackers : st
        })

        console.log(this.state.trackers)

    }

    deleteItem = (id) => {
        const idx = this.state.trackers.map(item => item.id).indexOf(id);
        this.setState(state => {
            let { trackers } = state;
            delete trackers[idx];
            return trackers;
        })

    }

    // componentDidUpdate() {
    //     window.localStorage.setItem('state', JSON.stringify(this.state));
    // }
    //
    // componentDidMount() {
    //     // there is a chance the item does not exist
    //     // or the json fails to parse
    //     try {
    //         const state = window.localStorage.getItem('state');
    //         this.setState({ ...JSON.parse(state) });
    //     } catch (e) {}
    // }


    render() {

        const {trackers} = this.state;
        return (
            <div className='App'>
                <Header/>
                <Input onInputSubmit = {this.onInputSubmit}/>
                {trackers.map(el=>{
                    return (<ItemList tracker = {el} key={el.id} deleteItem = {() => this.deleteItem(el.id)}/>)
                })}
            </div>
        )
    }
}

export default App;
