import React, {Component} from 'react';
import moment from "moment";
import './item-list.css';

class ItemList extends Component{


    state = {
        hours: 0,
        minutes : 0,
        sec : 0,
        pause : false
    }

    timer = null;



        componentDidMount(){
            this.timer = setInterval(() => {
                this.timers()
            }, 1000)
        }

        begincount = () => {


            this.timer = setInterval(() => {
                this.timers()
            }, 1000)
            let pauseStatus = this.state.pause;
            this.setState({
                pause : !pauseStatus
            })
            console.log(this.state)
            console.log(this.state.pause)
        }

    // startTime () {
    //     let _this = this;
    //     this.timer = setInterval(function () {
    //         _this.timers();
    //     },1000)
    // }




     timers = () => {
        let h = this.state.hours;
        let m = this.state.minutes;
        let s = this.state.sec;

        s++

        if(s>59){
            m++
            s = 0;
        }
        if(m>59){
            h++
            m = 0
        }
        this.setState({
            hours: h,
            minutes: m,
            sec:s
        })
    }

    pauseTime = () => {
        clearInterval(this.timer);
        let pauseStatus = this.state.pause;
        this.setState({
            pause : !pauseStatus
        })
        this.timer = null;
        console.log(this.state)
        console.log(this.state.pause)
    }

    cls = () => {
         if(this.state.pause === true) {
             return 'btn pause-btn paused'
         }else{
             return 'btn pause-btn'
         }
    }

    itemListClass = () =>{
        if(this.state.pause === true) {
            return 'ItemList'
        }else{
            return 'ItemList item-paused'
        }
    }




render() {
            // let timers = () => {
            //     let h = this.state.hours;
            //     let m = this.state.minutes;
            //     let s = this.state.sec;
            //
            //     s++
            //
            //     if(sec>=59){
            //         m++
            //         s = 0;
            //     }
            //     if(m>59){
            //         h++
            //         m = 0
            //     }
            //     this.setState({
            //         hours: h,
            //         minutes: m,
            //         sec:s
            //     })
            // }

            let display = (el) => {
                if(el<10){
                    return '0'+el
                }else{
                    return el
                }
            }



            let {hours, minutes , sec, pause} = this.state;

            return (
                <div className={this.itemListClass()}>
                    <h3 className='item-title'>{this.props.tracker.title}</h3>
                    <h3 className='item-time'>{`${display(hours)}:${display(minutes)}:${display(sec)}`}</h3>
                    <div>
                    <button className={this.cls()} onClick={pause ? this.begincount : this.pauseTime}></button>
                    <button className="btn delete-btn" onClick={this.props.deleteItem}></button>
                    </div>

                </div>
            )
        }


}

export default ItemList;
