import React, {Component} from 'react';
import moment from "moment";
import './Input.css';

class Input extends Component {

    submitInput = () => {
        let value = document.querySelector('.input-text').value;
        let name = '';
        if ( value === '') {
            name = moment().format('MMMM Do YYYY, h:mm:ss a');
        }else {
            name = value;
        }
        this.props.onInputSubmit(name)
        document.querySelector('.input-text').value = '';
    }

    onEnterPress = (e) => {
        if(e.keyCode === 13) {
            this.submitInput()
        }

    }

    render() {

        return (
        <div className='Input'>
            <form action="#">
            <input type="text" className='input-text' placeholder='Enter tracker name' onKeyPress={this.onEnterPress}/>
            <input type="submit" value="" className="input-submit" onClick={this.submitInput}  />
            </form>
        </div>
        )
    }

}

export default Input;